<template>
  <div id="NavBar">
    <van-nav-bar 
      :fixed="finalOption.fixed" 
      :title="finalOption.title" 
      :left-text="finalOption.leftText"
      :right-text="finalOption.rightText" 
      :left-arrow="finalOption.isLeft" 
      @click-left="onClickLeft" />
  </div>
</template>

<script>
export default {
  name: "NavBar",
  props: {
    options: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      finalOption: {
        isLeft: true,
        fixed: true,
        leftText: "返回",
        rightText: "",
        title: "",
      },
    };
  },
  mounted() {
    let finalOption = this.finalOption;
    Object.assign(finalOption, this.options);
    this.finalOption = finalOption;
  },

  methods: {
    onClickLeft() {
      const { name, query } = this.$route;
      console.log(this.$route, "routerName");
      const routerList = [
        "404Entry", //404页面
      ];
      if (!routerList.includes(name) || !!query.isInnerFrom) {
        this.$router.back(-1);
      } else {
        this.handleClose();
      }
    },
  }
};
</script>
<style lang="scss" src='./index.scss' scoped></style>



